import cloneDeep from 'lodash/cloneDeep'
import isEqual from 'lodash/isEqual'
import { useCallback, useContext, useMemo } from 'react'
import { useSelector } from 'react-redux'

import applyKeymapChanges from 'keymap-editor-zmk-backend/lib/apply-keymap-changes'
import extractKeymapData from 'keymap-editor-zmk-backend/lib/extract-keymap-data'

import { stripLegacyProperties } from '../../keymap'
import { ClipboardContext } from './store'

const keymapSelector = state => state.keyboard?.keymap
const settingsSelector = state => state.experimentalSettings

export function useKeymapUpdater () {
  const appKeymapState = useSelector(keymapSelector)
  const { enableBehaviorEditing = false } = useSelector(settingsSelector)
  const [{ selection }, actions] = useContext(ClipboardContext)

  const updateNeeded = useMemo(() => (
    appKeymapState &&
    selection.keymap &&
    !isEqual(appKeymapState, selection.keymap)
  ), [appKeymapState, selection.keymap])

  const update = useCallback(async function () {
    // When opening the clipboard dialog and a previously parsed keymap differs
    // from the app's current keymap state we're probably wanting to update the
    // keymap source
    const { layouts } = selection.metadata
    const preferredLayout = appKeymapState.chosen['zmk,matrix_transform']
      ? appKeymapState.chosen['zmk,matrix_transform'].replace(/^&/, '')
      : null

    const defaultLayout = Object.keys(layouts)[0]
    const { layout } = (
      layouts[preferredLayout] ||
      layouts[defaultLayout]
    )

    try {
      const raw = await applyKeymapChanges(layout, cloneDeep(appKeymapState), selection.keymapRaw, { enableBehaviorEditing })
      const parsed = await extractKeymapData(raw)
      stripLegacyProperties(parsed)

      actions.setKeymap({ raw, parsed })
      return raw
    } catch (err) {
      console.error('Failed to update/parse', appKeymapState)
      console.error(err)
      throw err
    }
  }, [appKeymapState, selection, actions, enableBehaviorEditing])

  return [updateNeeded, update]
}
