import { useContext } from 'react'

import ActionButton from '../../Common/ActionButton'
import DialogBox from '../../Common/DialogBox'
import Modal from '../../Common/Modal'
import Link from '../../Common/Link'
import {
  GitHubIssuesLink,
  DiscordProfileLink
} from '../../Dialogs/support'

import { ClipboardContext } from './store'

const discussionUrl = 'https://github.com/nickcoutsos/keymap-editor/discussions/105'
const DiscussionLink = (
  <Link external href={discussionUrl}>
    Feature Discussion
  </Link>
)

export default function FeedbackDialog () {
  const [, { closeFeedbackModal }] = useContext(ClipboardContext)

  return (
    <Modal onDismiss={closeFeedbackModal}>
      <DialogBox dismissText={null}>
        <h2>Beta Feedback</h2>
        <p>Hey, feel free to ignore this!</p>
        <p>
          I just launched this feature very quickly, I hope it's working well
          for you! Your feedback, if you can spare a moment, would be immensely
          helpful to me. This feature is completely browser-based which means I
          have no server analytics, so even just letting me know that it worked
          would be cool!
        </p>
        <p>Good ways to contact me:</p>
        <ul>
          <li>Ping me at {DiscordProfileLink}</li>
          <li>File an issue at {GitHubIssuesLink}</li>
          <li>Join the {DiscussionLink}</li>
        </ul>
        <div style={{ textAlign: 'center' }}>
          <ActionButton onClick={closeFeedbackModal}>
            Back to editor
          </ActionButton>
        </div>
      </DialogBox>
    </Modal>
  )
}
