import { useCallback, useContext, useMemo } from 'react'

import { Select } from '../Controls'
import { ClipboardContext } from './store'
import KeyboardAddButton from './KeyboardAddButton'

export default function KeyboardSelector () {
  const [{ keyboards, selectedKeyboard: selectedKeyboardId }, { selectedKeyboard }] = useContext(ClipboardContext)

  const selectedKeyboardChoice = useMemo(() => (
    keyboards.find(keyboard => keyboard.id === selectedKeyboardId)
  ), [keyboards, selectedKeyboardId])

  const handleSelectKeyboard = useCallback(choice => {
    selectedKeyboard({ keyboard: choice.id })
  }, [selectedKeyboard])

  if (keyboards.length === 0) {
    return <KeyboardAddButton />
  }

  return (
    <Select
      value={selectedKeyboardChoice}
      options={keyboards}
      onChange={handleSelectKeyboard}
      getOptionLabel={option => option.name || '<unnamed>'}
      getOptionValue={option => option.id}
      formatOptionLabel={option => option.name || <em>[unnamed]</em>}
    >
      <KeyboardAddButton />
    </Select>
  )
}
