import { useCallback, useContext } from 'react'

import Icon from '../../Common/Icon'
import { ClipboardContext } from './store'

export default function KeyboardAddButton () {
  const [, { selectedKeyboard, addKeyboard, openModal }] = useContext(ClipboardContext)

  const handleAddKeyboard = useCallback(() => {
    const id = Date.now()
    addKeyboard({ id })
    selectedKeyboard({ keyboard: id })
    openModal()
  }, [addKeyboard, selectedKeyboard, openModal])

  return (
    <button onClick={handleAddKeyboard}>
      <Icon name="gear" /> Add Keyboard
    </button>
  )
}
