import cloneDeep from 'lodash/cloneDeep'
import { createContext, useMemo, useReducer } from 'react'
import { createSlice } from '@reduxjs/toolkit'

const STORAGE_KEY_KEYBOARDS = 'keymap_editor_clipboard_slots'
const STORAGE_KEY_KEYBOARD = `${STORAGE_KEY_KEYBOARDS}:`
const STORAGE_KEY_SELECTED_KEYBOARD = 'keymap_editor_clipboard_selected_slot'

const BLANK_KEYBOARD_ENTRY = JSON.stringify({
  keyboardId: null,
  isCustom: true,
  keymap: null,
  keymapRaw: '',
  metadata: null,
  metadataRaw: ''
})

function initialState () {
  const storedKeyboardId = JSON.parse(localStorage.getItem(STORAGE_KEY_SELECTED_KEYBOARD) || 'null')

  const INITIAL_KEYBOARDS = JSON.parse(localStorage.getItem(STORAGE_KEY_KEYBOARDS) || '[]')
  const INITIAL_KEYBOARD_SLOT_ID = storedKeyboardId || INITIAL_KEYBOARDS[0]?.id

  const INITIAL_KEYBOARD_DATA = JSON.parse(
    localStorage.getItem(`${STORAGE_KEY_KEYBOARD}${INITIAL_KEYBOARD_SLOT_ID}`) || 'null'
  )

  const INITIAL_KEYBOARD_ID = INITIAL_KEYBOARD_DATA?.keyboardId || 'custom'
  const INITIAL_KEYBOARD_IS_CUSTOM = INITIAL_KEYBOARD_DATA?.isCustom || INITIAL_KEYBOARD_ID === 'custom'
  const INITIAL_KEYMAP_PARSED = INITIAL_KEYBOARD_DATA?.keymap
  const INITIAL_KEYMAP_RAW = INITIAL_KEYBOARD_DATA?.keymapRaw
  const INITIAL_METADATA_PARSED = INITIAL_KEYBOARD_DATA?.metadata
  const INITIAL_METADATA_RAW = INITIAL_KEYBOARD_DATA?.metadataRaw

  return {
    keyboards: INITIAL_KEYBOARDS,
    selectedKeyboard: INITIAL_KEYBOARD_SLOT_ID,
    modalOpen: !INITIAL_KEYMAP_RAW,
    feedbackModalOpen: false,
    selection: {
      keyboardId: INITIAL_KEYBOARD_ID,
      isCustom: INITIAL_KEYBOARD_IS_CUSTOM,
      keymap: INITIAL_KEYMAP_PARSED,
      keymapRaw: INITIAL_KEYMAP_RAW,
      metadata: INITIAL_METADATA_PARSED,
      metadataRaw: INITIAL_METADATA_RAW
    },
    pending: {}
  }
}

export const pickerSlice = createSlice({
  name: 'clipboard-picker',
  initialState,
  reducers: {
    selectedKeyboard (state, action) {
      state.selectedKeyboard = action.payload.keyboard

      const key = `${STORAGE_KEY_KEYBOARD}${state.selectedKeyboard}`
      state.selection = JSON.parse(localStorage.getItem(key) || BLANK_KEYBOARD_ENTRY)

      localStorage.setItem(STORAGE_KEY_SELECTED_KEYBOARD, JSON.stringify(state.selectedKeyboard))
    },
    addKeyboard (state, action) {
      const { id, name = null } = action.payload
      const keyboard = { id, name }

      state.keyboards.push(keyboard)
      localStorage.setItem(STORAGE_KEY_KEYBOARDS, JSON.stringify(state.keyboards))
      localStorage.setItem(`${STORAGE_KEY_KEYBOARD}${id}`, BLANK_KEYBOARD_ENTRY)
    },
    setKeymap (state, action) {
      const { raw, parsed, pending = false } = action.payload
      if (!state.pending) {
        state.pending = cloneDeep(state.selection)
      }
      state.pending.keymap = parsed
      state.pending.keymapRaw = raw
      if (!pending) {
        state.selection.keymap = parsed
        state.selection.keymapRaw = raw
      }
    },
    setMetadata (state, action) {
      const { keyboardId, isCustom = false, raw, parsed, pending = false } = action.payload
      if (pending && !state.pending) {
        state.pending = cloneDeep(state.selection)
      }
      const target = pending ? state.pending : state.selection
      target.keyboardId = keyboardId || target.keyboardId
      target.isCustom = isCustom
      target.metadata = parsed
      target.metadataRaw = raw
    },
    confirmPending (state) {
      state.selection = state.pending
      state.pending = null

      // TODO: should have an action to create and select the initial slot and
      // have Picker automatically dispatch when mounted with 0 keyboards.
      if (!state.selectedKeyboard) {
        state.selectedKeyboard = Date.now()
      }

      const key = `${STORAGE_KEY_KEYBOARD}${state.selectedKeyboard}`
      localStorage.setItem(key, JSON.stringify(state.selection))

      if (!state.keyboards.length) {
        state.keyboards.push({ id: state.selectedKeyboard })
      }

      const keyboard = state.keyboards.find(keyboard => keyboard.id === state.selectedKeyboard)
      if (!keyboard.name) {
        keyboard.name = state.selection.metadata.name
        localStorage.setItem(STORAGE_KEY_KEYBOARDS, JSON.stringify(state.keyboards))
      }
    },
    openModal (state) {
      state.modalOpen = true
      state.pending = state.selection
      if (state.pending.metadataRaw && !state.pending.metadata) {
        try {
          state.pending.metadata = JSON.parse(state.pending.metadataRaw)
        } catch (err) {}
      }
    },
    closeModal (state) {
      state.modalOpen = false
    },
    openFeedbackModal (state) { state.feedbackModalOpen = true },
    closeFeedbackModal (state) { state.feedbackModalOpen = false }
  }
})

function useActionDispatchers (dispatch) {
  return useMemo(() => (
    Object.keys(pickerSlice.actions).reduce((actions, action) => {
      actions[action] = (...args) => dispatch(pickerSlice.actions[action](...args))
      return actions
    }, {})
  ), [dispatch])
}

export function usePickerStore () {
  const [state, dispatch] = useReducer(pickerSlice.reducer, null, initialState)
  const dispatchers = useActionDispatchers(dispatch)

  return [state, dispatchers]
}

export const ClipboardContext = createContext([{}, {}])

export function PickerStore ({ children }) {
  const [state, dispatchers] = usePickerStore()

  return (
    <ClipboardContext.Provider value={[state, dispatchers]}>
      {children}
    </ClipboardContext.Provider>
  )
}

export function withPickerStore (Component) {
  return props => (
    <PickerStore>
      <Component {...props} />
    </PickerStore>
  )
}
