import { useCallback, useEffect, useRef, useState } from 'react'

import { validateInfoJson, InfoValidationError } from 'keymap-layout-tools/lib/validate.js'

import styles from './styles.module.css'

export default function MetadataRawInput ({ value, onChange, ...props }) {
  const ref = useRef()
  const [{ raw, error }, setState] = useState({ raw: value, parsed: null, error: null })

  const handleMetadataChange = useCallback(event => {
    const raw = event.target.value
    let parsed = null
    let error = null

    try {
      parsed = JSON.parse(raw)
      validateInfoJson(parsed)
    } catch (err) {
      error = err
      parsed = null
    }

    setState({ raw, parsed, error })
    onChange({ raw, parsed })
  }, [setState, onChange])

  useEffect(() => {
    setState(state => ({ ...state, raw: value }))
  }, [value, setState])

  return (
    <>
      <textarea
        ref={ref}
        onChange={handleMetadataChange}
        value={raw}
        {...props}
      />
      {error && (
        <p className={styles.parseError}>
          {error instanceof InfoValidationError ? error.errors[0] : error.toString()}
        </p>
      )}
    </>
  )
}
