import { useCallback, useContext } from 'react'

import Modal from '../../Common/Modal'
import DialogBox from '../../Common/DialogBox'
import ActionButton from '../../Common/ActionButton'

import { ClipboardContext } from './store'
import KeymapRaw from './KeymapRawInput'
import MetadataSelector from './MetadataSelector'

export default function KeymapSourceDialog ({ onSubmit, onCancel }) {
  const [state, actions] = useContext(ClipboardContext)
  const { pending } = state

  const handleKeymapChange = useCallback(({ raw, parsed }) => {
    actions.setKeymap({ pending: true, raw, parsed })
  }, [actions])

  const handleSubmit = useCallback(() => {
    actions.confirmPending()
    onSubmit({
      keymap: pending.keymap,
      metadata: pending.metadata
    })
  }, [actions, pending, onSubmit])

  const disabled = (
    !pending?.metadata ||
    !pending?.keymap
  )

  return (
    <Modal onDismiss={onCancel}>
      <DialogBox style={{ maxWidth: '640px', maxHeight: '90vh', overflow: 'auto' }} dismissText={null}>
        <MetadataSelector value={state.selection.keyboardId} />

        <h2>Keymap</h2>
        <p>Paste the devicetree source from your <code>.keymap</code> file below.</p>
        <p>
          You can <em>technically</em> make changes directly to the DeviceTree
          code here, but this isn't intended as an editor.
        </p>
        <KeymapRaw
          cols={80}
          rows={22}
          wrap="off"
          spellCheck={false}
          value={state.selection.keymapRaw}
          onChange={handleKeymapChange}
        />
        <p style={{ textAlign: 'center' }}>
          <ActionButton disabled={disabled} onClick={handleSubmit}>Set Keymap</ActionButton>
          <ActionButton onClick={onCancel} secondary>Cancel</ActionButton>
        </p>
      </DialogBox>
    </Modal>
  )
}
