import cloneDeep from 'lodash/cloneDeep'
import { useCallback, useContext, useEffect, useState } from 'react'

import { ClipboardContext, withPickerStore } from './store'
import ClipboardActions from './ClipboardActions'
import FeedbackDialog from './FeedbackDialog'
import KeyboardSelector from './KeyboardSelector'
import KeymapSourceDialog from './KeymapSourceDialog'

export function ClipboardPicker ({ onSelect, onError }) {
  const [state, { closeModal }] = useContext(ClipboardContext)
  const [initState, setInitState] = useState(null)

  const selectKeymap = useCallback((keymap, metadata) => {
    const { layouts, sensors = [] } = metadata
    const preferredLayout = keymap.chosen['zmk,matrix_transform']
      ? keymap.chosen['zmk,matrix_transform'].replace(/^&/, '')
      : null

    if (typeof layouts !== 'object') {
      console.error('Invalid metadata loaded', metadata)
      return
    }

    const defaultLayout = Object.keys(layouts)[0]
    const layoutMetadata = (
      layouts[preferredLayout] ||
      layouts[defaultLayout]
    )

    onSelect(cloneDeep({
      source: 'clipboard',
      keyboard: '',
      layoutMetadata,
      layout: layoutMetadata.layout,
      keymap,
      sensors
    }))
  }, [onSelect])

  useEffect(() => {
    if (state.selection?.keymap && state.selection?.metadata) {
      selectKeymap(state.selection?.keymap, state.selection?.metadata)
    }
  }, [selectKeymap, state.selection?.keymap, state.selection?.metadata])

  const handleSelectKeymap = useCallback(({ keymap, metadata }) => {
    selectKeymap(keymap, metadata)
    closeModal()
  }, [selectKeymap, closeModal])

  useEffect(() => {
    if (initState) {
      return
    }

    setInitState(true)

    if (state.selection?.keymap && state.selection?.metadata) {
      selectKeymap(state.selection?.keymap, state.selection?.metadata)
    }
  }, [initState, setInitState, selectKeymap, state])

  return (
    <>
      <KeyboardSelector />
      <ClipboardActions />
      {state.modalOpen && (
        <KeymapSourceDialog
          onSubmit={handleSelectKeymap}
          onCancel={closeModal}
        />
      )}
      {state.feedbackModalOpen && <FeedbackDialog />}
    </>
  )
}

export default withPickerStore(ClipboardPicker)
