import { useCallback, useContext, useMemo, useState } from 'react'

import keyboardCatalog from 'keymap-editor-contrib/keyboard-catalog.json'

import useToggle from '../../Common/hooks/use-toggle'
import { GitHubWikiLayoutsLink } from '../../Dialogs/support'
import { Select } from '../Controls'
import MetadataRawInput from './MetadataRawInput'
import styles from './styles.module.css'
import { ClipboardContext } from './store'

const choices = [
  { id: 'custom', name: 'Custom...' },
  ...Object.values(keyboardCatalog)
]

export default function MetadataSelector ({ value, onChange }) {
  const [{ pending }, actions] = useContext(ClipboardContext)

  const [sectionExpanded, toggleSection, expandSection] = useToggle(!pending?.metadataRaw)
  const [selection, setSelection] = useState(value)
  const selectedChoice = useMemo(() => (
    choices.find(choice => choice.id === selection)
  ), [selection])

  const handleChange = useCallback(async choice => {
    setSelection(choice.id)

    if (choice.id === 'custom') {
      actions.setMetadata({ pending: true, keyboardId: 'custom', isCustom: true, raw: '', parsed: null })
      expandSection()
    } else {
      try {
        const metadata = await import(`keymap-editor-contrib/keyboard-data/${choice.id}.json`)
        actions.setMetadata({ pending: true, isCustom: false, keyboardId: choice.id, parsed: metadata, raw: '' })
      } catch (err) {
        console.error(`Failed to load metadata for ${choice.id}`, err)
      }
    }
  }, [actions, setSelection, expandSection])

  const handleMetadataChange = useCallback(({ raw, parsed, errors }) => {
    actions.setMetadata({
      pending: true,
      id: selectedChoice.id,
      custom: selectedChoice.id === 'custom',
      raw,
      parsed,
      errors
    })
  }, [selectedChoice, actions])

  return (
    <article className={styles.source} data-expanded={sectionExpanded}>
      <h2>
        <span onClick={toggleSection}>Keyboard</span> {(
          <div
            style={{
              display: 'inline-block',
              fontSize: 'initial',
              fontWeight: 'normal',
              verticalAlign: 'middle'
            }}
          >
            <Select
              value={selectedChoice}
              onChange={handleChange}
              options={choices}
              getOptionValue={option => option.id}
              getOptionLabel={option => option.name}
            />
          </div>
        )}
      </h2>
      {selectedChoice?.id === 'custom' && (
        <section className={styles.sourceContent}>
          <p>
            In order to understand what keyboard you're using and now to display
            it visually, please paste the JSON source metadata file below.
          </p>
          {pending.metadata && (
            <p>Parsed metadata for: {pending.metadata.name}</p>
          )}
          <MetadataRawInput
            cols={80}
            rows={pending.metadata ? 22 : 5}
            wrap="off"
            spellCheck={false}
            value={pending.metadataRaw}
            onChange={handleMetadataChange}
          />
          {!pending.metadata && (
            <div className={styles.metadataHelp}>
              <h3>Not sure what to put here?</h3>
              <p>
                The <em>Keymap Editor</em> app depends on a defined keyboard
                layout both to provide a friendly graphical representation to
                edit and a readable textual representation. You can learn more
                about this at {GitHubWikiLayoutsLink}.
              </p>
              <p>
                Note: If your keyboard works with QMK's Configurator you may
                be able to quickly get a visual representation by pasting the
                contents of that keyboard's <code>info.json</code> file here.
              </p>
            </div>
          )}
        </section>
      )}
    </article>
  )
}
